html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


/* ----------- */



/* header */
.header{
    height: 958px;
    border-bottom: 1px solid #707070;
    background-image: url(../../assets/images/background.png);
    display: flex;
	justify-content: center;
	align-items: center;
    background-size: cover;
    background-position: center;
}
.titel{
    display: block;
    color: #fff;
    justify-content: center;
}
.header p{
    font-size: 24px;
}    
.header h1{
    font-size: 80px;
    font-weight: bold;
    margin-bottom: 32px;
}
.header ul{
    display: flex;
    margin-top: 40px;
}
.header a{
    margin-right: 16px;    
}

/* About */
/* .about{
    text-align: center;
    justify-content: center;
    align-items: center;
} */
.about {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-top: 56px;
    margin-bottom: 56px;
}
.about img{
    margin-top: 40px;
    margin-bottom: 16px;
}
.about p{
    margin-top: 2px;
    margin-bottom: 2px;
    line-height: 44px;
}
.about ul{
    list-style-type: disc;
}
.about li{
    margin-top: 28px;
}
.about ul li::before{
    content: '';
    background-color: #891818;
    width: 22px;
    height: 22px;
    display: inline-block;
    border-radius: 50%;
    margin-right: 16px;
}
.about hr{
    max-width: 384px;
}

/* brand */
.brand{
    background-color: #000000;
    border: 1px solid #707070;
    color: #fff;    
    text-align: center;
    font-weight: bold;
    padding-bottom: 160px;
}
.brand h2{
    margin-top: 56px;
}

.brand h3{
