}

.brand p{
    font-size: 20px;
    margin-top: 12px;
    margin-bottom: 28px;
    line-height: 30px;
}
.brand img{
    margin-top: 40px;
}
.brand ul{
    display: flex;
    justify-content: center;
    margin-right: 24px;
}

/* other works */
.works{
